@import "../../style/utils/mixins.scss";

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;

  @include medium-large-screens {
    padding: 40px 100px;
  }

  &__logo {
    max-width: 145px;
    @include large-screens {
      max-width: 211px;
    }
    @include xsmall-screens {
      max-width: 80px;
    }
  }
}
