@import "../../../../style/utils/mixins.scss";

.descriptionHeaderBox {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-between;
  row-gap: 20px;
  @include medium-large-screens {
    flex-direction: row;
  }
}

.descriptionHeader {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  @include medium-screens {
    padding-right: 2.7em;
  }
  @include large-screens {
    max-width: 70%;
  }

  &__title {
    font-weight: 500;
    font-size: 1.6em;
    line-height: 1.2em;
    @include large-screens {
      font-size: 2.3em;
    }
  }

  &__subtitle {
    font-weight: 500;
    font-size: 1.1em;
    @include large-screens {
      font-size: 1.3em;
    }
  }
}

.descriptionMore {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: fit-content;

  @include medium-screens {
    min-width: fit-content;
  }

  @include medium-large-screens {
    flex-direction: column-reverse;
    gap: 10px;
  }

  @include large-screens {
    justify-content: center;
  }
}
